/* Global */
html {
  box-sizing: border-box;
}

*,*:before, *:after {
  box-sizing: inherit;
}

h1, h2 {
  font-family: 'Caveat', cursive;
}

h1 {
  font-size: 60px;
}

h2 {
  font-size: 38px;
  margin-top: 0;
}

a {
  color: #EF233C;
}

a:hover {
  text-decoration: none;
}

.content-wrap {
  max-width: 950px;
  margin: 0 auto;
  padding: 50px;
  overflow: auto;
}

.col-narrow {
  width: 30%;
  float: left;
  min-height: 175px;
}

.col-wide {
  width: 70%;
  float: left;
  padding-left: 20px;
}

/* Header*/
header {
  text-align: left;
}

header h1, header h2{
  color: Honeydew;
  margin: 0;
}

.profile-image {
  width: 150px;
  border-radius: 100px;
}

/* Main */
.projects {
  background: #EDF2F4;
}

.education {
  background: #8D99AE;
}

/* Footer */
footer {
  text-align: center;
}

footer h2{
  color: Honeydew;
}

.contact-info a{
  padding: 10px;
  margin: 2px;
  display: inline-block;
}

/* Header and Footer */
header, footer {
  background: #322A61;
  color: #A8B2A8;
}
